        </div>
        <div class="content">
          <p class="tech"><b></b></p>
          <div class="skill">axios()<br>Vuex()<br>vue-router()<br>Vue2<br>SASS(SCSS)<br>Express(node.js)<br>Canvas()<br>ES6<br>localStorage<br>Webpack<br>CSS3<br>HTML5</div>
        </div>
        <div class="link">
          <p class="url"><b>:</b>&nbsp&nbsp&nbsp<a href="https://keephhh.com" target="_blank">keephhh</a></p>
          <p class="url"><b>:</b>&nbsp&nbsp&nbsp<a href="https://github.com/keephhh" target="_blank">github</a></p>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'about',
  data () {
    return {

    }
  },
  computed: {
    isShowAbout() {
      return this.$store.state.isShowAbout;
    }
  },
  methods: {
    showAbout() {
      this.$store.commit('showAbout', false)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../common/style/about.scss";
</style>



