<template lang="html">
  <div v-show="isShare" class="container">
    <div class="content">
      <div class="qq group" @click.stop.prevent="share">QQ</div>
      <div class="weixin group" @click.stop.prevent="share"></div>
      <div class="weibo group" @click.stop.prevent="share"></div>
    </div>
    <div @click.stop.prevent="hidMore" class="mask"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {

    }
  },
  computed: {
    isShare() {
      return this.$store.state.isShare
    }
  },
  methods: {
    hidMore() {
      this.$store.commit('share', false)
    },
    share() {
      alert('')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../common/style/sharemore.scss";

</style>



